* {
  box-sizing: border-box;
}
body,
h1,
p {
  margin: 0;
}
body {
  font-family: 'Hina Mincho', 'Helvetica Neue', Arial,
    'Hiragino Kaku Gothic ProN', 'Hiragino Sans', Meiryo, sans-serif;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
.header {
  background-color: transparent;
  mix-blend-mode: difference;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 40px;
  z-index: 1000;
}
.header-title {
  display: inline-block;
  width: 50%;
  color: #fff;
}

.hamburger {
  display: inline-block;
  width: 80px;
  height: 3px;
  background-color: #fff;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: -16px;
    background-color: inherit;
  }
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 16px;
    background-color: inherit;
  }
}
.main {
  background-color: #fff;
  width: 100%;
}
.content {
  width: calc(100% - 80px);
  margin-left: auto;
  margin-right: auto;
  min-height: 100vh;
}
.title {
  color: crimson;
  font-size: 8vw;
  font-weight: bold;
  font-feature-settings: 'palt';
  letter-spacing: 0.1em;
  white-space: nowrap;
  margin-top: 50vh;
  margin-bottom: 50vh;
}
.text {
  color: darkolivegreen;
  font-weight: bold;
  font-size: 24px;
  margin-top: 40px;
}
.img-wrap {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.gradient {
  height: 200vh;
  background: linear-gradient(
    0deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
}
.thank {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  font-size: 5vw;
  font-weight: bold;
	p{
		letter-spacing: .2em;
		font-feature-settings: "palt";
		text-align: center;
	}
}
